@import url('https://fonts.googleapis.com/css2?family=Pridi&family=Sono:wght@200&family=Kanit:wght@200&display=swap');


.body{
  background-color: #FDEFEF;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.contanier{
  width: 1000px;
  margin-top: 30px;
  display: flex;
  align-items: center;
  flex-flow: column wrap;
}

.header{
  width: 1000px;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column nowrap;
}

.title{
  margin: 0;
  font-size: 50px;
  font-family: 'Pridi', serif;
}

.headerDetail{
  background-color: #594e4cc4;
  width: 1000px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: row nowrap;
  border-bottom: 10px solid #594e4cc4;
  border-top: 8px solid #594e4cc4;
  border-radius: 10px 10px 0px 0px;

}

.input{
  all: unset;
  width: 400px;
  margin: 10px;
  border-bottom: 1px solid white ;
}

.input::placeholder{
  color: rgba(255, 255, 255, 0.521);
  font-family: 'Sono', sans-serif;
}

.btn{
  all: unset;
  font-family: 'Kanit', sans-serif;
  font-weight: bold;
  width: 50px;
  margin: 10px 5px;
  padding: 5px;
  font-size: 18px;
  text-align: center;
  cursor: pointer;
  border-radius: 10px;
  color: white; 
  border: 1px solid rgba(0,0,0,0.21);
  border-bottom-color: rgba(0,0,0,0.34);
  text-shadow:0 1px 0 rgba(0,0,0,0.15);
  box-shadow: 0 1px 0 rgba(255,255,255,0.34) inset, 
              0 2px 0 -1px rgba(0,0,0,0.13), 
              0 3px 0 -1px rgba(0,0,0,0.08), 
              0 3px 13px -1px rgba(0,0,0,0.21);
}

.btn:active {
  top: 1px;
  border-color: rgba(0,0,0,0.34) rgba(0,0,0,0.21) rgba(0,0,0,0.21);
  box-shadow: 0 1px 0 rgba(0,0,0,0.34),0 1px rgba(0,0,0,0.34) inset;
}

.addBtn{
  background-color: #D09E88;
}

.resetBtn{
  background-color: #e8c77a;
}


/*메모 리스트*/
.list{
  background-color: #CDBBA7;
  width: 1000px;
  height: 500px;
  padding: 10px 0px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-flow: column nowrap;
  overflow: scroll;
  overflow-x:hidden;
  border-radius: 0px 0px 10px 10px;
  -ms-overflow-style: none;
}

.list::-webkit-scrollbar {
  width: 5px;  /* 스크롤바의 너비 */
}

.list::-webkit-scrollbar-thumb {
  height: 20%; /* 스크롤바의 길이 */
  background: #3a3124; /* 스크롤바의 색상 */
  border-radius: 30px;
}

.list::-webkit-scrollbar-track {
  background: #CDBBA7;  /*스크롤바 뒷 배경 색상*/
}

.meno{
  overflow:hidden;
  width: 900px;
  min-height : 70px;
  background-color: #ffe9b6;
  margin: 5px;
  position: relative;
  border-radius: 10px;
  box-shadow: 0 2px #524c49;
}

.menoDetail{
  padding: 10px;
  display:  Inline-Block;
  width: 840px;
  word-break:break-all;
}

.delBtn{
  all: unset;
  position: absolute;
  left: 870px;
  top: 10px;
  cursor: pointer;
}

.time{
  margin: 5px 10px;
  font-size: 13px;
  display: flex;
  justify-content: flex-end;
}